<template>
  <!--背景图-->

  <div class = "note" :style = "note">
    <!--login框，表单+tab标签页的组合-->
    <div class = "loginFrame">
      <el-form method="post" label-position = "left" label-width = "0px" class = "demo-ruleForm login-container">
        <el-form-item prop = "account">
          <el-input type = "text" v-model="loginForm.username" auto-complete = "off" placeholder = "请输入您的账号"></el-input>
        </el-form-item>
        <el-form-item prop = "checkPass">
          <el-input type = "password" v-model="loginForm.password" auto-complete = "off" placeholder = "请输入密码"></el-input>
        </el-form-item>
        <el-form-item style = "width:100%;">
          <el-button type = "primary" style = "width:100%;" v-on:click="login">登录</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>

</template>

<script>
  export default {

    name: 'login',
    data() {
      return {
        rules: {
          account: [{required: true, message: '请输入用户名', trigger: 'blur'}],
          checkPass: [{required: true, message: '请输入密码', trigger: 'blur'}]
        },
        loginForm: {
          username: 'admin',
          password: '123'
        },
        note: {
          position:"absolute",
          top:"0px",
          left:"0px",
          width: "100%",
          height:"100%",
          backgroundImage: "url(" + require("../assets/loginback.jpg") + ")",
          backgroundSize: "100% 100%",
          backgroundRepeat: "no-repeat",
        },
      };
    },
    methods:{
      login: function (event){
        let md5 = require("crypto").createHash("md5");
        md5.update(this.loginForm.password);
        let params = this.$qs.stringify({
          username:this.loginForm.username,
          password:md5.digest('hex')
        });
        this.$http({
          method: 'post',
          url: '/login',
          data:params
        }).
        then( (response)=> {
          let data = response.data;
          if (data.status == "error")
            this.$message.error({message: data.msg});
          else{
            this.$store.commit('login', data.msg);  //保存session
            var path = this.$route.query.redirect;
            this.$router.push({path: path == '/' || path == undefined ? '/home' : path}); //路由跳转
          }
        }).catch( (error)=> {
          console.log(error);
        })
      }
    }
  }</script>

<style>

  .login-container {
    box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin: 180px auto;
    width: 350px;
    padding: 35px 35px 35px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }</style>



